import React from "react";

function ServiceNavbar() {
  return (
    <nav
      className="navbar is-active is-link laaps-background is-fixed-top is-transparent "
      role="navigation"
      aria-label="main navigation"
    >
      <div className="navbar-brand">
        <div className="navbar-item is-expanded">
          <h4 className="title is-6 has-text-white">Solicitar Lavado</h4>
        </div>
        <div
          role="button"
          id="burger"
          className="navbar-burger burger"
          aria-label="menu"
          aria-expanded="false"
          data-target="mobile-nav"
        >
          <span aria-hidden="true"></span>
          <span aria-hidden="true"></span>
          <span aria-hidden="true"></span>
        </div>
      </div>
      <div className="navbar-menu laaps-background">
        <div className="navbar-start">
          <div className="navbar-item">Cuenta</div>
          <div className="navbar-item">Historial</div>
          <div className="navbar-item">Mis Autos</div>
          <div className="navbar-item">Ayuda</div>
        </div>
        <div className="navbar-end">
        <div className="navbar-item">Cerrar Sesión</div>
        </div>
      </div>
    </nav>
  );
}

export default ServiceNavbar;
