import React from 'react';
import { withRouter } from "react-router-dom";
import ServiceNavbar from "../servicenavbar/ServiceNavbar";
import Modal from "../modal/Modal";
import washme from "../../assets/images/washme.png";
import "../../assets/styles/profile.css";

// const modal = () => {
//     history.push("/SignUp");
//   };

function ServiceRequest({ history }) {
    const [isActive, setisActive] = React.useState(false);
    
    return (
        <section className="section background-map">
            <ServiceNavbar />
            <div className="control">
                <div className="file is-centered fixed-bottom"
                onClick={() => {
                    setisActive(!isActive);
                  }}
                >
                    <img
                    className="img-car"
                    src={washme}
                    />
                </div>
            </div>
            <div className={`${isActive ? "" : "init-hidden"}`}>
            <div className="service-request box fixed-bottom">
                <Modal />
            </div>
            </div>
        </section>
    )
}

export default withRouter(ServiceRequest);
