import React, {useState } from "react";
import { withRouter } from "react-router-dom";

function Modal() {
  const [brand, setBrand] = useState("");
  const [model, setModel] = useState("");
  const [plateNumber, setPlateNumber] = useState("")

  return (
    <section>
      <div className="">
        <div classNamefile="field is-centered">
          <select className="select line-style laaps-background" onChange={(e) => setBrand(e.target.value)}>
            <option value="0">Marca</option>
            <option value="Nissan">Nissan</option>
            <option value="Chevrolet">Chevrolet</option>
            <option value="Ford">Ford</option>
            <option value="Audi">Audi</option>
            <option value="Alfa Romeo">Alfa Romeo</option>
          </select>
          <select className="select line-style laaps-background" onChange={(e) => setModel(e.target.value)}>
            <option value="0">Modelo</option>
            <option value="Fiesta">Fiesta</option>
            <option value="Spark">Chevrolet</option>
            <option value="Tsuru">Tsuru</option>
            <option value="Audi A3">Audi A3</option>
            <option value="Stelvio">Stelvio</option>
          </select>
          <input
            id="plateNumber"
            className="input line-style"
            type=""
            placeholder="Placas"
            onChange={(e) => setPlateNumber(e.target.value)}
          />
        </div>
      </div>
    </section>
  );
}

export default withRouter(Modal);
